import React, { Component } from 'react'
import CanvasJSReact from './../assets/canvas/canvasjs.react'
let CanvasJS = CanvasJSReact.CanvasJS
let CanvasJSChart = CanvasJSReact.CanvasJSChart

export default class Upper extends Component {
	constructor(props) {
		super()
		this.state = {
			value: 'BTC',
			days: 12,
			newDate: 10,
			newCoin: 'BTC',
			total: [],
			totalAct:[],
			options: {},
			kry:""
		}
		this.handleClick = this.handleClick.bind(this)
		this.handleChange = this.handleChange.bind(this)
		this.handleDays = this.handleDays.bind(this)
		this.chartC = this.chartC.bind(this)
	}

	handleClick() {
		this.chartC()
	}

	async getData() {
		try {
			const url = `http://13.90.227.9/query?coin=${this.state.value}&days=${this.state.days}`
			console.log(url)

			let data = await fetch(url)
			data = await data.json()
			// console.log(data.pre,data.act)
			return [data.pre,data.act,data.kry]
		} catch (err) {
			console.error(err)
		}
	}
	async chartC() {
		this.setState({ total: [], options: {},totalAct:[],kry:"" })
		CanvasJS.addColorSet('greenShades', [
			'#2F4F4F',
			'#008080',
			'#2E8B57',
			'#3CB371',
			'#90EE90'
		])
		try {
			let data = await this.getData()
			// let act = data.act

			data[0] = data[0].split('\n')
			data[1] = data[1].split('\n')
			this.setState({kry:data[2]})

			let base = data[1].length-2
			base=data[0][base].split(',')
			let baseDate = base[0].split('-')
			this.state.total.push({
				x: new Date(
					parseInt(baseDate[0],10),
					parseInt(baseDate[1]-1,10),
					parseInt(baseDate[2],10)
				),
				y: parseInt(data[1][data[1].length-2].split(',')[1], 10)
			})
			

			for (let i = data[1].length-1; i < data[0].length; i++) {
				const e = data[0][i].split(',')
				let date = e[0].split('-')
				this.state.total.push({
					x: new Date(
						parseInt(date[0],10),
						parseInt(date[1]-1,10),
						parseInt(date[2],10)
					),
					y: parseInt(e[1], 10)
				})
			}
			for (let i = data[1].length-30+this.state.days; i < data[1].length; i++) {
				const e = data[1][i].split(',')
				let date = e[0].split('-')
				this.state.totalAct.push({
					x: new Date(
						parseInt(date[0],10),
						parseInt(date[1]-1,10),
						parseInt(date[2],10)
					),
					y: parseInt(e[1], 10)
				})
				// break;
			}

			this.setState({
				options: {
					backgroundColor: 'rgba(0, 128, 255, 1)',
					animationEnabled: true,
					zoomEnabled: true,
					theme: 'light1',
					title: {
						// text: 'Data Plot'
					},
					axisX: {
						labelFontColor: 'white',
						valueFormatString: 'DD-MMM',
						labelAngle: -50,
						color:'black'
					},
					axisY: {
						labelFontColor: 'white',
						title: 'Price in USD',
						prefix: '$',
						includeZero: false
					},
					legend: {
						cursor: 'pointer',
						verticalAlign: 'top',
						horizontalAlign: 'center',
						dockInsidePlotArea: true
						// itemclick: toogleDataSeries
					},
					data: [
						{
							markerColor: 'white',
							color: 'white',
							type: 'line',
							name: 'prediction',
							showInLegend: true,
							dataPoints: this.state.total
						},
						{
							markerColor: 'black',
							color: 'black',
							type: 'line',
							name: 'actual',
							showInLegend: true,
							dataPoints: this.state.totalAct
						}
					]
				}
			})
		} catch (err) {
			console.log('API Down')
		}
	}
	async componentWillMount() {
		this.chartC()
	}

	componentDidMount() {
		let chart = this.chart
		chart.render()
	}

	handleChange(event) {
		this.setState({ value: event.target.value })
	}

	handleDays(e) {
		this.setState({ days: e.target.value })
	}

	render() {
		return (
			<React.Fragment>
				<section className="upper row">
					<div className="above col-md-6 col-sm-6">
						<div className="currency row">
							<div className="currName col-3">CRYPT</div>
							<input
								className="currVal col-6"
								id="coin"
								onChange={this.handleChange}
								value={this.state.value}
							/>
						</div>{' '}
						<div className="currency row">
							<div className="currName col-3">DAYS</div>
							<input
								id="days"
								onChange={this.handleDays}
								type="text"
								className="currVal col-6"
								value={this.state.days}
							/>

							{/* </input> */}
						</div>
						<div className="row currency update">
							<div
								onClick={this.handleClick}
								className="col-8 updateVal"
								>
								<p>Update</p>
							</div>
						</div>
						<div className="krypton">
								<h3>Kryptons:  {this.state.kry}</h3>
						</div>
					</div>
					<div className="col-sm-6 col-md-6 chart">
						<CanvasJSChart
							options={this.state.options}
							onRef={ref => (this.chart = ref)}
						/>
						<div className="container">Bitcoin - BTC</div>
						<div className="container">Litecoin - LTC</div>
						<div className="container">Ethereum - ETH</div>
						<div className="container">Zcash - ZEC</div>
						<div className="container">Dogecoin - DOGE</div>
						<div className="container">NEO - NEO</div>
						<div className="container">Bitcoin Cash - BCH</div>
					</div>
				</section>
			</React.Fragment>
		)
	}
}
